.link-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 1rem 0;
    gap: 1.5rem;
}

.link-img {
    height: auto;
    width: 40vw;
    border-radius: 14px;
}

@media (max-width: 1200px) {
    .link-img {
        width: 50vw;
    }
}

@media (max-width: 900px) {
    .link-img {
        width: 60vw;
    }
}

@media (max-width: 700px) {
    .link-img {
        width: 70vw;
    }
}

@media (max-width: 700px) {
    .link-img {
        width: 70vw;
    }
}

@media (max-width: 500px) {
    .link-img {
        width: 90vw;
    }
}